import {
  type JsonSearchParams,
  type RecursiveSearchParams
} from '../../types/utils'
import { appendFileSync } from 'fs'

let counter = 0
let totalCount = 0
const start = true
const version = 0

export const search = (query: JsonSearchParams, jsonObj: any): Array<any> => {
  // If nothing was requested, return the current object
  if (!query.keyToMatch) return [jsonObj]

  // Tokenize the search terms if it's never been done and call the recursive search
  const result = searchRec({
    valueToMatch: query.valueToMatch,
    tokenizeKeys: query.keyToMatch.split('.'),
    parent: jsonObj,
    tokensUsed: false,
    count: query.count || Infinity,
    results: []
  }, jsonObj)

  totalCount += counter
  let data = `Current Ticks: ${counter}\nTotal Ticks: ${totalCount}\n`
  if (start) data = `\n\nVersion: ${version}\n` + data
  appendFileSync('./benchmarks.txt', data)

  counter = 0
  return result
}

const searchRec = (query: RecursiveSearchParams, jsonObj: any): Array<any> => {
  counter++
  // If jsonObj is undefined we can't compare anything and so we fail early
  if (typeof jsonObj === 'undefined') return []
  // If we have enough results we can return
  if (query.results.length >= query.count) return query.results

  const { valueToMatch, tokenizeKeys, tokensUsed } = query

  // If we ran out of tokenizeKeys it means we need to verify
  // that we found the right key with the correct value and if so add the parent to the results list
  if (
    !tokenizeKeys.length &&
    (typeof valueToMatch === 'undefined' || valueToMatch === jsonObj)
  ) {
    query.results.push(query.parent)
  } else {
    query.parent = jsonObj
    query.tokensUsed = true
    // If we are searching in an Array, we would go over each child without moving the token counter
    if (Array.isArray(jsonObj)) {
      // console.log('query', query)
      jsonObj.forEach(child => searchRec({ ...query }, child))
    } else if (typeof jsonObj === 'object') {
      // If there not tokens was used, it means that we still have the full tokenizeKeys
      // So we will try to use the same tokenizeKeys for every child of jsonObj
      if (!tokensUsed) {
        for (const key in jsonObj) {
          searchRec({
            ...query,
            tokensUsed: false
          }, jsonObj[key])
        }
      }

      // Shift one level based on the first key in tokenizeKeys with jsonObj as the parent
      searchRec({
        ...query,
        tokenizeKeys: tokenizeKeys.slice(1)
      }, jsonObj[tokenizeKeys[0]])
    }
  }

  return query.results
}
